header {
  padding-top: 30px;
}

header .logo span {
  font-size: 24px;
}

header nav li:not(:last-child) {
  margin-right: 40px;
}

header nav li a:hover {
  color: #3ACF6E;
}

header nav li a:hover img {
  opacity: 0.6;
}

main h1 {
  font-size: 40px;
}

main h2 {
  font-size: 35px;
}

main .hero .hero-top .col:nth-child(2) {
  margin-bottom: 40px;
}

main .hero .row:nth-child(1) {
  margin-bottom: 100px;
}

main .hero .schema {
  font-size: 1em;
}

main #project {
  padding: 150px 0;
}

footer {
  padding: 200px 0;
}

footer .left a, footer .left span,
footer .right a,
footer .right span {
  font-size: 18px;
}

footer .left a {
  transition: all 300ms ease-in-out;
}

footer .left a:hover {
  color: #3ACF6E;
}

footer .logo h2 {
  font-size: 35px;
}

footer .logo img {
  width: 120px;
}

footer .logo span {
  font-size: 22px;
}

@supports not (-ms-high-contrast: none) {
  html.js .animation {
    opacity: 0;
    transform: translateY(30px);
  }
}

@media (prefers-reduced-motion: no-preference) {
  html.js .animation.animated {
    animation-name: fade-in-bottom;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
  }
}
